<template>
  <t-page ref="page">
    <bscroll
    ref="bscroll"
    :pullDownRefresh="{threshold: 50,stop: 50, txt: '页面已刷新'}"
    @pullingDown="pullDown">
      <t-all :data="data" @click="click"></t-all>
    </bscroll>
  </t-page>
</template>
<script>
import tAll from '@extend/grid'
import bscroll from '@extend/Bscroll'
export default {
  components: {
    tAll,
    bscroll
  },
  data () {
    return {
      data: []
    }
  },
  methods: {
    pullDown () {
      this.request({
        head:{
          timestamp: (new Date()).getTime()
        },
        data:{
          modelCode:'page_two'
        }
      }, () => {
        setTimeout(() => {
          this.$refs.bscroll.forceUpdate(false)
        }, 500)
      })
    },
    click (e) {
      // this.$toast({
      //   msg: `跳转类型 ${e.linkType}, 跳转地址 ${e.link}`
      // })
    },
    request (opt, callback) {
      T.Native.networkRequest({
        data: {
          url: '/operate/manager/queryModelList',
          params: opt
        }
      }).then((data) => {
        this.data = JSON.parse(data[0].json)
        callback && callback()
      })
    }
  },
  mounted() {
    this.request({
      head:{
        timestamp: (new Date()).getTime()
      },
      data:{
        modelCode:'page_two'
      }
    })
  },
}
</script>
<style lang="scss" scoped>
.loading{
        position: absolute;
    height: 0.5rem;
    width: 100%;
    text-align: center;
    margin: 0.2rem auto;
    top: -0.9rem;
    >img{
      height: 100%;
    }
  }
</style>




